import React, { useState, useEffect } from 'react';
import CommonSection from '../shared/CommonSection';

import 'F:/MernStack-Tour-Management-main/tour-management/frontend/src/styles/tour.css';
import TourCard from 'F:/MernStack-Tour-Management-main/tour-management/frontend/src/shared/TourCard.jsx';
import SearchBar from 'F:/MernStack-Tour-Management-main/tour-management/frontend/src/shared/SearchBar.jsx';
import Newsletter from 'F:/MernStack-Tour-Management-main/tour-management/frontend/src/shared/Newsletter.jsx';
import tourData from 'F:/MernStack-Tour-Management-main/tour-management/frontend/src/assets/data/tours.js';
import { Container, Row, Col } from 'reactstrap';
import { Link } from 'react-router-dom';










const Tours = () => {
   const [pageCount,setPageCount] = useState(0);
   const [page,setPage] = useState(0);

   useEffect(()=> {
    const pages  = Math.ceil(5 / 4) ;//later we will use backend data count
     setPageCount(pages);
    },[page]);

  return ( 
    
  <>
  
  <CommonSection title={"All Tours"}/>
  <section>
    <Container>
      <Row>
        <SearchBar/>
      </Row>
    </Container>
  </section>
  <section>
    <Container>
      <Row>
        {
          tourData?.map(tour=>(
          <Col lg= "3" className ="mb-4" key={tour.id}> 
          <TourCard tour={tour}/>
          </Col>
          ))}

          {tourData?.map((tour) => (
              tour && tour.photo && (
                
                <Col lg="3" className="mb-4" key={tour.id}>
                  <TourCard tour={tour} />
                  <Link to={`/tour/Rs{tour.id}`}>View Details</Link>
                </Col>
              )
            ))}

          
          <Col lg="12">
            <div className="pagination d-flex align-items-center
            justify-content-center mt-4 gap-3">
              {[...Array(pageCount).keys()].map(number=>(
              <span key={number} onClick = {()=> setPage(number)}
              className={page === number ? "active__page" : ""}
              >
               {number + 1}
              </span>
              ))}
            </div>
          </Col>
      </Row>
    </Container>
  </section>
  <Newsletter/>
  </>
  );
};

export default Tours;