import React from 'react';
import 'F:/MernStack-Tour-Management-main/tour-management/frontend/src/styles/tour-details.css';
import { Container, Row, Col, Form,ListGroup}  from 'reactstrap' ;
import {useParams} from 'react-router-dom';
import tourData from '../assets/data/tours';
import calculateAvgRating from '../utils/avgRating';

const TourDetails = () => {

   const {id} = useParams()
   //this is an static data later we will call our API and load our data from database

   const tour = tourData.find(tour => tour.id === parseInt(id));
   //destructure properties from tour object
   
  
   const { photo, title,desc, price, address, reviews, city,distance,  maxGroupSize } = tour;



    const {totalRating, avgRating} = calculateAvgRating(reviews)
   return(
   <>
   
   <section>
     <Container>
       <Row>
         <Col lg='8'> 
          <div className="tour__content">
            <img src={photo} alt=""/>

            <div className="tour__info">
            <h2>{title}</h2>

            <div className='d-flex align-items-center gap-5'>
              <span className='d-flex align-items-center gap-1' ></span>
              <span className="tour__rating d-flex align-items-center gap-1">
        <i class="ri-star-fill style={'color':var(--secondary-color)}"></i>{calculateAvgRating === 0 ? null : avgRating}
        {totalRating === 0 ? (
           'Not rated' 
           ) : ( 
           <span>({reviews?.length})</span>
           )}
        </span> 
          <span><i class="ri-map-pin-user-fill"></i> {address}
        </span>
            </div>

            <div className="tour__extre-details">
            <span><i class="ri-map-pin-2-line"></i> {city}</span>
            <span><i class="ri-money-dollar-circle-line"></i>Rs{price} / per person
            </span>
            <span>
              <i class="ri-group-line"></i>{maxGroupSize}
              </span>
          </div>
          <h5>Description</h5>
          <p>{desc}</p>
        </div>
        </div>
      </Col>
    </Row>

     </Container>
   </section>
   </>
   );
};

   


export default TourDetails;